const canvas = document.getElementById("background");
const ctx = canvas.getContext("2d");
canvas.width = window.innerWidth;
canvas.height = window.innerHeight;

let effect = null;
let gradient = ctx.createLinearGradient(0, 0, canvas.width, canvas.height);
gradient.addColorStop(0, "red");
gradient.addColorStop(0.2, "yellow");
gradient.addColorStop(0.4, "green");
gradient.addColorStop(0.6, "cyan");
gradient.addColorStop(0.8, "blue");
gradient.addColorStop(1, "magenta");

window.addEventListener("resize", (event) => {
	canvas.width = event.target.window.innerWidth;
	canvas.height = event.target.window.innerHeight;
	effect = new HackEffect(canvas.width, canvas.height);
});

class HackSymbol {
	constructor(x, y, fontSize, canvasHeight) {
		this.characters = "01";
		this.x = x;
		this.y = y;
		this.fontSize = fontSize;
		this.text = "";
		this.canvasHeight = canvasHeight;
	}
	draw(context) {
		var rand = Math.random() > 0.5;
		this.text = this.characters.charAt(Math.floor(Math.random() * this.characters.length));
		context.fillText(this.text, this.x * this.fontSize, this.y * this.fontSize);
		if (this.y * this.fontSize > this.canvasHeight && Math.random() > 0.98) {
			this.y = 0;
		} else {
			this.y += 1;
		}
	}
}

class HackEffect {
	constructor(canvasWidth, canvasHeight) {
		this.canvasWidth = canvasWidth;
		this.canvasHeight = canvasHeight;
		this.fontSize = 25;
		this.columns = this.canvasWidth / this.fontSize;
		this.symbols = [];
		this.initialize();
	}
	initialize() {
		for (let i = 0; i < this.columns; i++) {
			this.symbols[i] = new HackSymbol(i, 0, this.fontSize, this.canvasHeight);
		}
	}
	resize(width, height) {
		this.canvasWidth = width;
		this.canvasHeight = height;
		this.columsn = this.canvasWidth / this.fontSize;
	}
}

effect = new HackEffect(canvas.width, canvas.height);
let lastTime = 0;
const fps = 24;
const nextFrame = 1000 / fps;
let timer = 0;

function animate(timeStamp) {
	const deltaTime = timeStamp - lastTime;
	lastTime = timeStamp;
	if (timer > nextFrame) {
		ctx.fillStyle = `rgba(0,0,0,0.05)`;
		ctx.textAlign = "center";
		ctx.fillRect(0, 0, canvas.width, canvas.height);
		ctx.fillStyle = gradient;
		ctx.font = effect.fontSize + "px monospace";
		effect.symbols.forEach((symbol) => {
			symbol.draw(ctx);
		});
		timer = 0;
	} else {
		timer += deltaTime;
	}
	requestAnimationFrame(animate);
}
animate(0);